export default {
  getLocalUser () {
    return localStorage.user ? JSON.parse(localStorage.user) : {}
  },
  clone (obj) {
    return JSON.parse(JSON.stringify(obj))
  },
  checkStatus (statusCode, type = 'text') {
    statusCode = statusCode || 1
    let statusText = {
      1: {text: '审批中', color: '#FFAA00'},
      2: {text: '已驳回', color: '#E96767'},
      3: {text: '已通过', color: '#00C353'}
    }
    return statusText[statusCode][type]
  },
  /**
   *@desc 接收2个数据，返回true\false
   *@author lintaohai
   *@date 2018-08-22
   **/
  isNotEqual (x, y) {
    return x !== y
  },
  /**
   *@desc 拼接数据，用separator间隔
   *@author lintaohai
   *@date 2018-08-20
   **/
  joinName (arrUser = [], separator = '、') {
    return arrUser.join(separator)
  },
  /**
   *@desc 操作类型映射
   *@author yuanwei
   *@date 2018-08-21
   **/
  checkOPType (typeCode) {
    typeCode = typeCode || 1
    let opTypeText = {
      1: '新增角色',
      2: '修改角色',
      3: '删除角色',
      101: '新增申请',
      102: '通过申请',
      103: '驳回申请'
    }
    return opTypeText[typeCode]
  },
  checkLevel (typeCode) {
    let data = {
      1: '一级审批',
      2: '二级审批',
      3: '三级审批',
      4: '四级审批',
      5: '五级审批'
    }
    if (!typeCode) return data
    return data[typeCode]
  },
  checkStep (typeCode) {
    let data = {
      1: '申请人直属上级',
      2: '申请人部门负责人',
      3: '指定角色',
      4: '指定人员'
    }
    if (!typeCode) return data
    return data[typeCode]
  }
}



