import Service from '@/utils/http'
let baseA = '/application'
let baseB = '/approval'

export default {
  // 我的申请列表
  getApplicationList (params = {}) {
    return Service.get(`${baseA}/list`, {params})
  },
  // 我的申请详情
  getApplicationId (id = 0) {
    return Service.get(`${baseA}/` + id)
  },
  // 申请审批节点详情
  getApplicationNodeUser (applicationId, nodeId) {
    return Service.get(`${baseA}/${applicationId}/${nodeId}/user`)
  },
  // 查询我的申请节点指定用户详情
  getApplicationNodeUserDateil (applicationId, nodeId, roleId) {
    return Service.get(`${baseA}/${applicationId}/${nodeId}/user/${roleId}`)
  },
  // 查询我的申请节点指定角色的用户详情
  getApplicationNodeRoleDateil (applicationId, nodeId, roleId) {
    return Service.get(`${baseA}/${applicationId}/${nodeId}/role/${roleId}`)
  },
  // 撤回我的申请
  delApplicationId (id = 0) {
    return Service.delete(`${baseA}/${id}`)
  },
  /* ========== 区分线 ========== */
  // 我的审批列表
  getApprovalList (params = {}) {
    return Service.get(`${baseB}/list`, {params})
  },
  // 我的审批详情
  getApprovalId (id = 0) {
    return Service.get(`${baseB}/` + id)
  },
  // 通过我的审批
  putApprovalId (id = 0) {
    return Service.put(`${baseB}/${id}`)
  },
  // 驳回我的审批
  patchApprovalId (id = 0, data = {}) {
    return Service.patch(`${baseB}/${id}`, data)
  }
}



