import sysManage from './sysManage'
import approval from './approval'
import process from './process'
import userManage from './userManage'

const api = {
  ...sysManage,
  ...approval,
  ...process,
  ...userManage
}

const install = function (Vue) {
  Vue.prototype.$API = api
}

export default {
  install,
  ...api
}



