import Service from '@/utils/http'

export default {
  searchSys (params) {
    return Service.get('/resource/associated_system/list', {params})
  },
  addSys (data) {
    return Service.post('/resource/associated_system', data)
  },
  putSys (id, params) {
    return Service.put(`/resource/associated_system/${id}`, params)
  },
  reviseSys (id) {
    return Service.get(`/resource/associated_system/${id}`)
  },
  // 获取导航菜单
  getRbacMenu (params) {
    return Service.get('/rbac/menu')
  },
  // 获取用户信息
  getUserProfile (params) {
    return Service.get('/index/user/profile')
  },
  // 获取用户待审批信息
  getUserApprovalList (params) {
    return Service.get('/index/user/approval/list', {params})
  },
  // 获取用户关联系统
  getUserAssociatedSystem (params) {
    return Service.get('/index/user/associated_system')
  },
  // 查询角色列表
  getRoleList (params = {}) {
    return Service.get('/rbac/role/list', { params })
  },
  // 查询角色详情
  getRole (roleId) {
    return Service.get('/rbac/role/' + roleId)
  },
  // 新增角色
  addRole (params = {}) {
    return Service.post('/rbac/role', params)
  },
  // 修改角色
  putRole (roleId, params = {}) {
    return Service.put('/rbac/role/' + roleId, params)
  },
  // 删除角色
  delRole (roleId) {
    return Service.delete('/rbac/role/' + roleId)
  },
  // 获取组织部门
  getOrgTree (params = {}) {
    return Service.get('/rbac/organization/department', { params })
  },
  // 获取部门
  getDepartmentTree () {
    return Service.get('/rbac/organization')
  },
  // 查询用户绑定角色列表
  getUserRoleList (params = {}) {
    return Service.get('/rbac/user/role/list', { params })
  },
  // 解绑用户的职能角色
  unBindRole (params = {}) {
    return Service.post('/rbac/user/unbind_role', params)
  },
  // 查询用户列表
  getUserList (params = {}) {
    return Service.get('/user/list', { params })
  },
  // 查询权限管理报表列表
  getStatisticsPermission (params = {}) {
    return Service.get('/statistics/permission', {params})
  },
  // 获取组织部门的职能角色
  getRoleTree (params = {}) {
    return Service.get('/rbac/organization/role', { params })
  },
  // 绑定用户的职能角色
  bindRole (params = {}) {
    return Service.post('/rbac/user/bind_role', params)
  },
  // 查询用户的关联系统列表
  getUserSystem (userId) {
    return Service.get(`/audit/user/${userId}/associated_system`)
  },
  // 查询关联系统的用户列表
  getSystemUsers (systemId, params) {
    return Service.get(`/audit/associated_system/${systemId}/user`, { params })
  },
  // 退出登录
  loginOut () {
    return Service.get('http://10.125.20.45:8802/cas/v1/logout')
  }
}



