import axios from 'axios'
import {Message, Loading} from 'element-ui'

const ERROR_MSG_TIME = 3 * 1000
let authorization = sessionStorage.authorization
if (!authorization === true || authorization === 'undefined') {
  authorization = getQueryStringArgs('authorization')
  sessionStorage.setItem('authorization', authorization)
}

let baseURL = 'http://10.125.20.45:8811/saturn/admin/v1'
if (process.env.NODE_ENV === 'env') {
  if (process.__STG__) {
    baseURL = '333333333333'
  } else {
    baseURL = 'http://10.125.20.45:8811/saturn/admin/v1'
  }
}

const service = axios.create({
  timeout: 10000,
  baseURL: baseURL,
  // headers: {Authorization: authorization}
  // headers: {Authorization: 'ST-CSw95xeZPe6ZjkfLAAy6OA0rbxyJ2EP7c9L0VTmy9AjdgH66o6k81hr4fzOxn'},
  // 允许携带cookie
  withCredentials: true
})

// loading函数
const loading = {
  loadingTxt: '加载中...',
  length: 0,
  closeLength: 0,
  instantiation: false,
  open (txt) {
    txt = txt || this.loadingTxt
    this.length++
    this.instantiation = Loading.service({
      fullscreen: true,
      background: 'rgba(0, 0, 0, 0)',
      text: txt
    })
  },
  close () {
    if (++this.closeLength >= this.length && this.instantiation) {
      this.instantiation.close()
    }
  }
}

service.interceptors.request.use(params => {
  loading.open()
  return params
})

service.interceptors.response.use(response => {
  loading.close()
  let data = response.data
  if (data.code === 200) {
    return data
  } else if (data.code === 401) {
    window.location.href = '/login'
  } else {
    Message({
      message: data.message || '系统异常',
      type: 'error',
      duration: ERROR_MSG_TIME
    })
  }
  let msg = data.code ? data.message : `${response.config.headers['method']} : ${data.error}`
  data.message = msg
  return Promise.reject(data)
}, error => {
  loading.close()
  console.log(error.response.data)
  if (error.response && error.response.data && error.response.data.code === 403) {
    window.location.href = error.response.data.result.redirect_url
  } else if (error.code === 'ECONNABORTED' && error.message.indexOf('timeout') !== -1) {
    error.message = '请求超时，请重试'
  }
  Message({
    message: error.message || '系统异常',
    type: 'error',
    duration: ERROR_MSG_TIME
  })
  return Promise.reject(error)
})

// 提取链接上的校验数据 login?authorization=CSw95x
function getQueryStringArgs (key) {
  let search = location.search.length > 0 ? location.search.substring(1) : ''
  let items = search ? search.split('&') : []
  let args = {}

  for (let i = 0, len = items.length; i < len; i++) {
    let item = items[i].split('=')
    let k = decodeURIComponent(item[0])
    args[k] = decodeURIComponent(item[1])
  }
  if (key) return args[key]
  return args
}

export default service



