<template>
   <div style="visibility: hidden;">第三方登录中转页面</div>
</template>

<script>
import { Loading } from 'element-ui'
export default {
  data () {
    return {
      loading: ''
    }
  },
  beforeCreate () {
    this.loading = Loading.service({
      fullscreen: true,
      background: 'rgba(0, 0, 0, 0)',
      text: '登录中'
    })
    // 取到值后进入首页，否则跳到第三方登陆页
    let isHas = sessionStorage.authorization
    if (isHas !== 'undefined') {
      this.loading.close()
      this.$router.push('/index')
    } else {
      // location.href = 'http://www.baidu.com'
    }
  },
  methods: {
  }
}
</script>



