<template>
  <span @click="open">
    <slot>
      <span v-if="label">{{ label }}</span>
    </slot>
  </span>
</template>

<script>
export default {
  name: 'KyeConfirm',
  props: {
    label: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: '',
      required: true
    },
    content: {
      type: String,
      default: ''
    },
    callback: {
      type: Function
    },
    scopeData: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      duration: 1500
    }
  },
  methods: {
    open () {
      if (this.disabled) return false
      this.$confirm(this.content, this.title, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        cancelButtonClass: 'medium',
        confirmButtonClass: 'medium'
      }).then(() => {
        this.callback(this.scopeData.id).then(res => {
          if (res === true) {
            this.$message({
              type: 'success',
              message: '操作成功!',
              duration: this.duration
            })
          } else {
            this.$message({
              type: 'info',
              message: '操作失败',
              duration: this.duration
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消',
          duration: this.duration
        })
      })
    }
  }
}
</script>



