import Service from '@/utils/http'
let baseUrl = '/configuration/approval'

export default {
  // 查询审批流程配置列表
  getConfigurationApprovalList (params = {}) {
    return Service.get(`${baseUrl}/list`, {params})
  },
  // 查询审批流程配置关联系统
  getConfigurationApprovalSys () {
    return Service.get(`${baseUrl}/associated_system`)
  },
  // 查询审批流程配置详情
  getConfigurationApprovalId (params = {}) {
    return Service.get(`${baseUrl}/${params.id}`)
  },
  // 新增审批流程配置
  postConfigurationApproval (data = {}) {
    return Service.post(`${baseUrl}`, data)
  },
  // 修改审批流程配置
  putConfigurationApprovalId (id, data = {}) {
    return Service.put(`${baseUrl}/${id}`, data)
  },
  // 删除审批流程配置
  delConfigurationApprovalId (id) {
    return Service.delete(`${baseUrl}/${id}`)
  }
}



