<style lang="less" scoped>
  .el-button--small {
    width: 72px;
    font-size: 14px;
    padding: 9px 0;
  }
</style>
<template>
  <span @click="open">
    <slot>
      <span v-if="label">{{ label }}</span>
    </slot>
  </span>
</template>

<script>
export default {
  name: 'KyePrompt',
  props: {
    label: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: '',
      required: true
    },
    callback: {
      type: Function
    },
    scopeData: {
      type: Object,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      duration: 1500
    }
  },
  methods: {
    open () {
      if (this.disabled) return false
      this.$prompt('', this.title, {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputType: 'textarea',
        cancelButtonClass: 'medium',
        confirmButtonClass: 'medium'
      }).then(({ value }) => {
        console.log(value)
        if (!this.callback) return true
        this.callback(this.scopeData.id, value).then(res => {
          if (res.code === 200) {
            this.$message({
              type: 'success',
              message: '操作成功!',
              duration: this.duration
            })
          } else {
            this.$message({
              type: 'info',
              message: '操作失败',
              duration: this.duration
            })
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入',
          duration: this.duration
        })
      })
    }
  }
}
</script>



