<style lang="less" scoped>
.header {
  height: 60px;
  .flag {
    width: 4px;
    height: 20px;
    margin: 20px 0;
    background-color: #398cff;
  }
  .title {
    font-size: 18px;
    padding: 20px 0;
    line-height: 20px;
    margin-left: 10px;
    color: #333333;
  }
}
.el-header {
  border-bottom: 2px solid #dadde2;
}
</style>
<template>
<el-header>
  <div class="header">
    <div class="fl flag"></div>
    <div class="fl title">{{title}}</div>
    <div class="fr link">
      <slot></slot>
    </div>
  </div>
</el-header>
</template>
<script>
export default {
  name: 'TopHeader',
  data () {
    return {}
  },
  props: {
    title: {
      type: String,
      required: true
    }
  }
}
</script>



