import moment from 'moment'

export default {
  date (date = new Date(), fmt = 'YYYY-MM-DD') {
    return moment(date).format(fmt)
  },
  datetime (date = new Date(), fmt = 'YYYY-MM-DD HH:mm') {
    return moment(date).format(fmt)
  },
  // 文字前缩略  ...\xxx\xxx
  formatStr (str, width) {
    if (!width) return str
    // 文字大小 斜杆大小 ../大小
    const [font, i, j] = [12, 6, 16]
    function checkWidth (s) {
      let group = s.split('\\')
      let len = group.length
      let maxWidth = ((s.length - (len - 1)) * font) + ((len - 1) * i) + j
      console.log(maxWidth)
      if (maxWidth >= width) {
        let a = group.slice(1).join('\\')
        return checkWidth(a)
      } else {
        return `...\\${group.slice(0).join('\\')}`
      }
    }
    return checkWidth(str)
  }
}



