import Vue from 'vue'
import Router from 'vue-router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import Login from '@/views/login'
import API from '../api'
import Store from '../store'

Vue.use(Router)

let routerArr = [
  {
    path: '/login',
    component: Login
  },
  {
    path: '/test',
    component: () => import('@/test/test')
  },
  {
    path: '/',
    component: () => import('@/views/layout/home'),
    children: [
      {
        path: '/index',
        component: () => import('@/views/index')
      },
      {
        path: '/resource/system',
        component: () => import('@/views/resource/system')
      },
      {
        path: '/authority/role',
        component: () => import('@/views/authority/role')
      },
      {
        path: '/authority/user-role',
        component: () => import('@/views/authority/userRole')
      },
      {
        path: '/audit/user',
        component: () => import('@/views/audit/user')
      },
      {
        path: '/audit/system',
        component: () => import('@/views/audit/system')
      },
      {
        path: '/system-setup/approval',
        component: () => import('@/views/system-setup/approval')
      },
      {
        path: '/total-table/user-table',
        component: () => import('@/views/total-table/user-table')
      },
      {
        path: '/total-table/authority-table',
        component: () => import('@/views/total-table/authority-table')
      },
      {
        path: '/process-management/application',
        component: () => import('@/views/process-management/application')
      },
      {
        path: '/process-management/application/:id',
        component: () => import('@/views/process-management/application-detail')
      },
      {
        path: '/process-management/approval',
        component: () => import('@/views/process-management/approval')
      },
      {
        path: '/process-management/approval/:id',
        component: () => import('@/views/process-management/approval-detail'),
        props: true
      },
      {
        path: '/account-management/change-password',
        component: () => import('@/views/account-management/change-password'),
        props: true
      },
      {
        path: '/account-management/account-setup',
        component: () => import('@/views/account-management/account-setup'),
        props: true
      },
      {
        path: '/account-management/login-setup',
        component: () => import('@/views/account-management/login-setup'),
        props: true
      },
      {
        path: '/account-management/other-login-setup',
        component: () => import('@/views/account-management/other-login-setup'),
        props: true
      },
      {
        path: '*',
        redirect: '/index'
      }
    ]
  }
]

const router = new Router({
  mode: 'history',
  routes: routerArr
})
router.beforeEach((to, from, next) => {
  if (to.path === from.path) {
    next(false)
    return
  }
  NProgress.start()
  if (!Store.state.userInfo) {
    API.getUserProfile().then((res) => {
      Store.commit('getUserInfo', res.result || {})
      next()
    }).catch(() => {
      next()
    })
  } else {
    next()
  }
})

router.afterEach(() => {
  NProgress.done()
})

export default router



